"use client";
import { Input } from "@/components/ui/input";
import { Search, Filter, ChevronDown } from "lucide-react";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu"

export default function Orders() {

  return (
    <section className="relative min-h-screen min-w-full overflow-hidden space-y-5 mt-18 mx-3">
      <h1 className="text-xl font-bold text-[#2E7D32]">Orders</h1>
      <div className="flex flex-row gap-2 justify-between space-x-4 mt-auto p-5 bg-white rounded-md shadow-md">
              <Input
                type="text"
                placeholder="Search for products or vendors"
                className="flex-1 bg-[#f3f3f5]"
              />

              <DropdownMenu>
                    <DropdownMenuTrigger asChild> 
                        <Filter className="mr-2 h-4 w-4 text-[#2E7D32]" /> {/* ðŸ§© green filter icon */}
                        Filter
                        <ChevronDown className="ml-auto h-4 w-4 text-[#2E7D32]" /> {/* dropdown arrow */}
                      
                    </DropdownMenuTrigger>

                    <DropdownMenuContent
                      side="top"
                      align="start"
                      sideOffset={8}
                      className="w-[--radix-popper-anchor-width] z-[9999]"
                    >
                      {/* Example filter options */}
                      <DropdownMenuItem>
                        <Check className="mr-2 h-4 w-4 text-[#2E7D32]" /> All
                      </DropdownMenuItem>
                      <DropdownMenuItem>
                        Pending
                      </DropdownMenuItem>
                      <DropdownMenuItem>
                        Completed
                      </DropdownMenuItem>
                      <DropdownMenuItem>
                        Cancelled
                      </DropdownMenuItem>
                    </DropdownMenuContent>
              </DropdownMenu>
            </div>
    </section>
  )
}
